/** @format */

import React from 'react';
import { UserProfileForm } from 'components/UserProfileForm';
import { FiltersIcon } from 'components/Filter/FiltersBtn.styled';
import ModalWindow, { customStyles } from 'components/Modal';
import { closeIconStyles } from './EditProfileModal.styled';

export const EditProfileModal = ({ isModalState, statehangeModal }) => {
	return (
		<ModalWindow
			isOpen={isModalState}
			onRequestClose={statehangeModal}
			style={customStyles}
			contentLabel='onRequestClose'
		>
			<FiltersIcon name='close' onClick={statehangeModal} style={closeIconStyles} />

			<UserProfileForm statehangeModal={statehangeModal} isModalState={isModalState} />
		</ModalWindow>
	);
};
